@import "src/styles/Mixins.scss";
@import "src/styles/Variables.scss";

.searchInputBlock {
  width: 40%;
  position: relative;

  @media (max-width: 400px) {
    @include size(16px, 16px);
    background: url("../../Images/searchIcon.svg") no-repeat center;
  }

  .searchInput {
    @include SFTextRegular();
    font-size: 15px;
    line-height: 20px;
    color: $textColor;
    padding: 14px 40px 12px;
    gap: 16px;
    width: 100%;
    height: 36px;
    border: 1px solid #A7ACB1;
    outline: none;
    border-radius: 4px;
    @media (max-width: 400px) {
      display: none;
    }

    & {
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M6 10C3.791 10 2 8.209 2 6C2 3.791 3.791 2 6 2C8.209 2 10 3.791 10 6C10 8.209 8.209 10 6 10ZM15.707 14.293L10.887 9.473C11.585 8.492 12 7.296 12 6C12 2.687 9.313 0 6 0C2.687 0 0 2.687 0 6C0 9.313 2.687 12 6 12C7.296 12 8.492 11.585 9.473 10.887L14.293 15.707C14.488 15.902 14.744 16 15 16C15.256 16 15.512 15.902 15.707 15.707C16.098 15.316 16.098 14.684 15.707 14.293Z' fill='%235C5F62'/%3e%3c/svg%3e");
      background-repeat: no-repeat;
      background-position: 2%;
    }

    &::placeholder {
      @include SFTextRegular();
      font-size: 15px;
      line-height: 20px;
      color: $opacityColor;
    }
  }
}

.searchInputBlockForHeader {
  position: relative;
  width: 96%;

  .searchInput {
    padding: 14px 40px 12px;
    gap: 16px;
    width: 100%;
    height: 36px;
    border-radius: 5px;
    border: 1px solid #A7ACB1;
    position: relative;

    & {
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M6 10C3.791 10 2 8.209 2 6C2 3.791 3.791 2 6 2C8.209 2 10 3.791 10 6C10 8.209 8.209 10 6 10ZM15.707 14.293L10.887 9.473C11.585 8.492 12 7.296 12 6C12 2.687 9.313 0 6 0C2.687 0 0 2.687 0 6C0 9.313 2.687 12 6 12C7.296 12 8.492 11.585 9.473 10.887L14.293 15.707C14.488 15.902 14.744 16 15 16C15.256 16 15.512 15.902 15.707 15.707C16.098 15.316 16.098 14.684 15.707 14.293Z' fill='%235C5F62'/%3e%3c/svg%3e");
      background-repeat: no-repeat;
      background-position: 2%;
    }

    & ::placeholder {
      @include SFTextRegular();
      font-size: 15px;
      line-height: 20px;
      color: $opacityColor;
    }
  }
}




