@import "src/styles/Mixins";

.loadingContainer {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  background: #fff;
  z-index: 1001;

  .loadingIcon {
    position: relative;
    top: 50%;
    left: 50%;
    width: 50px;
    height: 50px;
    margin-top: -32px;
    margin-left: -32px;
    background: url("../../../Images/Spinner-1s-210px.gif") no-repeat 50% 50%;
    object-fit: contain;
  }
}